export default {
	data() {
		return {
			globalData: {},
			list: [],
			failureList: [],
			checkedList: [],
			isAll: false,
			total_amount: 0,
			moveCurId: null,
			listTouchStart: 0,
			listTouchDirection: null
		};
	},
	onLoad() {
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		loadData() {
			this.$minApi
				.cart()
				.then(res => {
					for (let i = 0; i < res.data.length; i++) {
						if (i % 2 === 0) {
							this.list.push(res.data[i]);
						} else {
							this.failureList.push(res.data[i]);
						}
					}
					getApp().globalData.cart_count = this.list.length
				})
				.catch(err => {
					console.log(err);
				});
		},
		CheckboxChange(id) {
			let index = this.checkedList.indexOf(id);
			if (index > -1) {
				this.checkedList.splice(index, 1, 0);
			} else {
				this.checkedList.push(id);
			}
			this.countAmount();
		},
		AllCheckboxChange() {
			this.isAll = !this.isAll;
			this.checkedList = [];
			if (this.isAll) {
				for (let i = 0; i < this.list.length; i++) {
					this.checkedList.push(this.list[i].id);
				}
			}
			this.countAmount();
		},
		DelCheckboxChange(index) {
			let that = this;
			uni.showModal({
				title: '提示',
				content: '商品已失效，是否删除？',
				success: function(res) {
					if (res.confirm) {
						that.del(0, index);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		countAmount() {
			console.log(this.checkedList);
			let total_amount = 0;
			for (let i = 0; i < this.list.length; i++) {
				if (this.checkedList.indexOf(this.list[i].id) > -1) {
					total_amount += this.list[i].goods.price * this.list[i].num;
				}
			}
			this.total_amount = total_amount.toFixed(2);
		},
		editNum(index, num) {
			this.$minApi
				.cartEdit({ id: this.list[index].id, num: num })
				.then(res => {
					this.list[index].num = num;
				})
				.catch(err => {
					console.log(err);
				});
		},
		del(type, index) {
			this.$minApi
				.cartDel({ id: type ? this.list[index].id : this.failureList[index].id })
				.then(res => {
					if (type) {
						this.list.splice(index, 1);
					} else {
						this.failureList.splice(index, 1);
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		toGoodsDetails(id) {
			uni.navigateTo({
				url: '/pages/goods/details?id=' + id
			});
		},
		toPay() {
			let cart = this.checkedList.join(',')
			if(!cart){
				uni.showToast({
					icon: 'none',
					title: "请选择要购买的商品",
					duration: 2000
				});
				return false;
			}
			uni.navigateTo({
				url: '/pages/order/ready?cart=' + cart
			});
		},
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX;
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left';
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.moveCurId = e.currentTarget.dataset.target;
			} else {
				this.moveCurId = null;
			}
			this.listTouchDirection = null;
		}
	}
};
